  export default {
    async asyncData({
      $content,
      params
    }) {
      const artworks = await $content ('artworks').only(['title', 'description', 'category', 'img', 'cover', 'slug', 'createdAt']).sortBy('createdAt', 'asc').fetch()

      return {
        artworks
      }
    },
    methods: {
      formatDate(date) {
        const options = {
          year: 'numeric',
          month: 'long',
          day: 'numeric'
        }
        return new Date(date).toLocaleDateString('en', options)
      }
    },
}

